/*Nút đăng ký*/
.btn-Register{
    cursor: pointer;
    border-style: none;
    /* border-color: blue; */
    border-radius: 10px;
    color: white;
    font-weight: bold;
    background-color: purple;
    width: 375px;
    font-size: 20px;
    height: 50px;
}
.btn-Register:hover{
    background-color: blue;
}
.label-2{
    margin-left: 20%;
    font-size: 17px;
    color: black;
}
/*Login with another account*/
.Login-Account{
    margin-left: 20%;
}
.Login-Account div{
    margin-left: 10px;
}
.Microsoft-Account{
    background-image: url("../src/assets/img/microsoft.png");
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: 40px;
    border-style: groove;
    border-width: 1px;
    border-color: #3498DB;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    float: left;
}
